
.stars {
  display: flex;

  &__star {
    background: url(/images/star.svg) no-repeat center;
    width: 16px;
    height: 16px;
  }
  
  &__star:not(:last-child) {
    margin-right: 4px;
  }
  
  &__star:nth-child(-n+1),
  &__star:nth-child(-n+2),
  &__star:nth-child(-n+3),
  &__star:nth-child(-n+4) {
    background-image: url(/images/star-active.svg);
  }
}
